.sidebarChat {
	display: flex;
	padding: 20px;
	cursor: pointer;
	border-bottom: 1px solid #f6f6f6;
}

.sidebarChat:hover {
	background-color: #ebebeb;
}
.sidebarChat__info > h2 {
	font-size: 16px;
	margin-bottom: 8px;
}
.sidebarChat__info > p {
	font-size: 14px;
	margin-bottom: 8px;
	word-wrap: break-word;
}
.sidebarChat__info {
	margin-left: 15px;
	text-transform: capitalize;
}
.sidebarChat__delete {
	margin-left: auto;
}
.sidebarChat__delete:hover {
	color: #fc5b5b;
}
